<template>
<div>
  <nav class="mynav">
    <div class="nav-bottom" @click="navtab1()">
    <router-link to="/home">
      <i :class="{'color-red1':ishome}" class="iconfont icon-shouye"></i>
      <!-- <span>15</span> -->
      <p :class="{'color-red1':ishome}">首页</p>
    </router-link>
    </div>



    <div class="nav-bottom" @click="navtab2()">
      <router-link to="/subjectlist">
        <i :class="{'color-red1':issc}" class="iconfont icon-favorite"></i>
        <p :class="{'color-red1':issc}">学习</p>
      </router-link>
    </div>

    <div class="nav-bottom" @click="navtab3()">
      <router-link to="/vedio/record">
        <i :class="{'color-red1':ismessage}" class="iconfont icon-comments"></i>
        <!-- <span>2</span> -->
        <p :class="{'color-red1':ismessage}">记录</p>
      </router-link>
    </div>

    <div class="nav-bottom" @click="navtab4()">
      <router-link to="/mine">
        <i :class="{'color-red1':iswode}" class="iconfont icon-account"></i>
        <p :class="{'color-red1':iswode}">我的</p>
      </router-link>
    </div>
  </nav>
</div>
</template>

<style>
.mynav{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0rem 0;
  border-top: 1px solid #e5e5e5 ;
  font-size: 0;
  background: #f5f5f5;
  z-index: 999;
}
.nav-bottom{
  display: inline-block;
  width: 25%;
  text-align: center;
  position: relative;
}
.nav-bottom i{
  font-size: 2rem;
  color: #999;
}
.nav-bottom p{
  color: #333;
  font-size: 0.8rem;
  margin-top: -0.6rem;
}
.nav-bottom span{
  position: absolute;
  top:0.4rem;
  right: 18%;
  background: #FF5722;
  border-radius: 1rem;
  padding: 0rem 0.45rem;
  border: 1px solid #fff;
  color: #fff;
  font-size: 0.8rem;
  text-align: center;
}
.color-red1{
  color: #FF5722!important;
}
</style>

<script>
export default {
  data(){
    return{
      ishome: true,
      issc: false,
      ismessage: false,
      iswode: false,
    }
  },
  mounted() {
    this.$nextTick(function () {

      var uri = this.$route.path;

      if (uri === '/home'){
        this.ishome   = true;
        this.issc   = false;
        this.ismessage   = false;
        this.iswode = false;
      }
      if (uri === '/mine') {
        this.ishome   = false;
        this.issc   = false;
        this.ismessage   = false;
        this.iswode = true;
      }
      if (uri === '/subjectlist') {
        this.ishome   = false;
        this.issc   = true;
        this.ismessage   = false;
        this.iswode = false;
      }
      if (uri === '/vedio/record') {
        this.ishome   = false;
        this.issc   = false;
        this.ismessage   = true;
        this.iswode = false;
      }

    })

  },
  methods: {
    navtab1() {
      this.ishome   = true;
      this.issc   = false;
      this.ismessage   = false;
      this.iswode = false;
    },
    navtab2(){
      this.ishome   = false;
      this.issc   = true;
      this.ismessage   = false;
      this.iswode = false;
    },
    navtab3(){
      this.ishome   = false;
      this.issc   = false;
      this.ismessage   = true;
      this.iswode = false;
    },
    navtab4(){
      this.ishome   = false;
      this.issc   = false;
      this.ismessage   = false;
      this.iswode = true;
    },
  }
}

</script>




