import Vue from 'vue'
import Router from 'vue-router'

import login from '@/pages/login'
import home from '@/pages/home'
import mine from '@/pages/mine'
import photograph from '@/pages/vedio/photograph.vue'
import subjectlist from '@/pages/subjectlist.vue'
import vediolist from '../pages/vedio/vediolist.vue'
import vedioplay from '../pages/vedio/vedioplay.vue'
import vedioImgList from '../pages/vedio/vedioImglist.vue'
import vediorecord from '../pages/vedio/vediorecord.vue'
import html5photograph from '../pages/vedio/html5photograph.vue'
Vue.use(Router)

const router  = new Router({

  routes: [

    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    },
    {
      path: '/subjectlist',
      name: 'subjectlist',
      component: subjectlist
    },
    {
      path: '/photograph',
      name: 'photograph',
      component: photograph
    },
    {
      path: '/vedio/html5photograph',
      name: 'html5photograph',
      component: html5photograph
    },
    {
      path: '/vedio/list',
      name: 'vediolist',
      component: vediolist
    },

    {
      path: '/vedio/ImgList',
      name: 'ImgList',
      component: vedioImgList
    },
    {
      path: '/vedio/play',
      name: 'vedioplay',
      component: vedioplay
    },
    {
      path: '/vedio/record',
      name: 'vediorecord',
      component: vediorecord
    }

  ]
})

//白名单，配置了才排除
const whiteList=['/','/login'];
router.beforeEach((to,from,next)=>{
    if(whiteList.indexOf(to.path)>-1)
    {
      next();
    }else
    {
        if(localStorage.getItem(tokenId)==null)
        {
          next({path:'/login',query:{redirect:to.fullPath}});
        }else
        {
          next();
        }
    }
});
export default router;



