<template>
<div style="padding-top: 3rem; padding-bottom: 4rem;">
<my-header head-title="驾培视频"></my-header>



  <ul class="videolist">
    <li v-for="item in items">
    <div class="videolist-wrap">
      <div class="videolist-btn" @click="view(item)">

        <div class="done-img" v-if="item.StudyType==1"><img src="../../assets/ywc.png"></div>
        <!-- <i class="iconfont icon-iconfontwancheng" v-if="item.StudyType==1"></i> -->
        <!-- <img src="../../assets/sp.png"> -->
        <div class="playicon"><img src="../../assets/spplay.png"></div>
     <p>{{item.CourseUnit}} 时长:{{item.CourseLength}}秒 {{parseInt(item.StudyPace==0?0:item.StudyPace/item.CourseLength*100)}} %</p>
      <div class="processcontainer">
        <div id="processbar" :style="{'width':+(item.StudyPace/item.CourseLength*100)+'%'}"></div>
      </div>
     </div>
     <div class="videolist-text">
     <h2> 《{{item.CourseName}}》</h2>

   </div>
   </div>
    </li>
  </ul>

  <mydialog :eventObj="eventObj" :dialogmsg="dialogmsg" :doconfirm="doconfirm" :docancel="docancel" :isshowdialog="isshowdialog"></mydialog>

  <myalert :isshowalert="isshowalert" :alertmsg="alertmsg" :doclose="doclose"></myalert>
  <bottombar></bottombar>
</div>
</template>

<script>

  import myheader from '../../components/myheader.vue'
  import bottombar from '../../components/bottombar'
    export default{

      components:{
          'my-header':myheader,
          'bottombar':bottombar,
      },
      data(){
          return {
            pwidth:60,
            isshowalert:false,
            alertmsg:"",
            showload: false,
            loadtext: "正在加载",
            dialogmsg:"确定观看下一课",
            isshowdialog:false,
            eventObj:{event:"",param:""},
            items : [],
            courseType:'',
            course:""
         }
      },

      mounted(){

        let courseType = this.$route.query.courseType;
        this.courseType=courseType;
        //console.log(courseType);
        this.showload=true;
        this.axios.post(baseurl+'/api/vedio/list.html?courseType='+this.courseType).then(
                   response =>{
                     this.showload=false;
                      this.items = response.data;
                      //console.log(response);

                    },
                    response =>{
                        this.showload=false;
                        this.isshowalert=true;
                        this.alertmsg="请求失败，请重试！"
                   },
               );
      },
      methods: {
        view(obj)
        {
            this.course=obj;
            if(obj.StudyType==1)
            {
                this.dialogmsg="该课程已经完成，你需要重看该课程吗？";
            }else
            {
              //this.dialogmsg="你要学习该课程吗";
              //this.$router.push({path:'/photograph',query:{courseNo:this.course.CourseNo,courseType:this.courseType}});
              this.$router.push({path:'/vedio/play',query:{courseNo:this.course.CourseNo,courseType:this.courseType}});
            }
           // console.log(obj);
           this.isshowdialog=true;
        },
        doclose()
        {
          this.isshowalert=false;
        },
        doconfirm(eventObj)
        {
           this.isshowdialog=false;
          this.$router.push({path:'/vedio/play',query:{courseNo:this.course.CourseNo,courseType:this.courseType}});
        },
        docancel()
        {
          this.isshowdialog=false;
        }
       },

    }
</script>

<style>

.videolist-wrap{
  display: inline-block;
  width: 95%;
}
.videolist-text{
  text-align: left;
}
.videolist-text h2{
  width: 95%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 1rem;
  font-weight: bold;
  color: #333;
}
.videolist-text p{
  width: 95%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.8rem;
  font-weight: normal;
  color: #999;
}
.videolist-btn{
    width:100%;
    height:8rem;
    border:1px solid #e5e5e5;
    background: #EFEFEF;
    border-radius: 0.4rem;
    overflow: hidden;
    position: relative;
}
.playicon{
  position: absolute;
  width: 4rem;
  height: 4rem;
  left: 50%;
  top: 50%;
  margin-left: -2rem;
  margin-top: -2.5rem;
  z-index: 9;
}
.playicon img{
  width: 100%;
  height: 100%;
}
.videolist-btn img{
  width: 100%;
  height: 100%;
}
.done-img{
  position: absolute;
  right: 0;
  top: -0.5rem;
  width: 4.5rem;
  height: 4.5rem;
  z-index: 99;
}
.done-img img{
  width: 100%;
  height: 100%;
}
.videolist-btn i{
  position: absolute;
  right: 0;
  top: -0.3rem;
  color: #fff;
  font-style: normal;
  font-size: 0.8rem;
}
.videolist-btn p{
    width:100%;
    padding: 0 0.5rem;
    height:1.6rem;
    line-height: 1.6rem;
    color: #fff;
    font-size: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 0.4rem;
    background: rgba(0,0,0,0.5);
    text-align: left;
  }
  .processcontainer{
    width:100%;
    height:0.4rem;
    /*border:1px solid #e5e5e5;*/
    background: #e5e5e5;
    overflow: hidden;
    position: relative;
    position: absolute;
    left: 0;
    bottom: 0;
  }

  #processbar{
    background:#4CAF50;
    float:left;
    height:100%;
    text-align:center;
    line-height:150%;
  }

  .video-list-title{
    padding: 0.6rem 1rem;
    border-bottom: 1px solid #e5e5e5;
  }
  .video-list-title h2{
    font-size: 1rem;
    color: #333;
    font-weight: bold;
    /*margin-bottom: 0.8rem;*/
  }
  .video-list-title h2 span{
    margin-left: 0.4rem;
    font-weight: bold;
  }
  .video-list-title p{
    font-size: 1rem;
    color: #999;
    margin-top: 0.6rem;
    float: left;
    margin-right: 1rem;
  }
  .videolist{
    padding: 0 1rem;
  }
  .videolist li{
    display: inline-block;
    margin: 0.6rem 0 0 0;
    width: 50%;
    text-align: center;
  }
  .videolist-con{
  padding: 1rem 1rem;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
}
.videolist-con span{
  font-size: 1rem;
  color: #333;
  margin-right: 1rem;
  font-weight: bold;
  display: inline-block;
  width:45%;
}
.videolist-con span:first-child{
  width: 18%;
}

.videolist-con i{
  position: absolute;
  right: 1rem;
  top: 0.6rem;
  font-size: 1rem;
  color: #fff;
  font-style: normal;
  padding: 0.3rem 1.5rem;
  background: #FF5722;
  border-radius: 0.3rem;
}
</style>






