<template>
<div style="padding-top: 3rem; padding-bottom: 4rem;">
<my-header head-title="学习记录"></my-header>



  <ul>
    <li v-for="item in this.items">
      <div class="rec-list">
        <h2><span>学习时间：</span>{{item.StartTime}} 至 {{item.EndTime}}</h2>
        <h2><span>科目：</span>{{item.CourseTypeName}}</h2>
        <h2><span>课程名称：</span>《 {{item.CourseName}}》</h2>
      </div>
    </li>
  </ul>
  <div v-if="last==false" @click="loadmore()" style="text-align: center;font-size: 1rem;color: #999;background: #fff;padding: 0.6rem;">点击加载更多</div>
  <div v-if="last==true" style="text-align: center;font-size: 1rem;color: #999;background: #fff;padding: 0.6rem;">没有更多记录</div>
  <myalert :isshowalert="isshowalert" :alertmsg="alertmsg" :doclose="doclose"></myalert>
  <loadpanel :showload="showload" :loadtext="loadtext"></loadpanel>
  <bottombar></bottombar>
</div>
</template>

<style type="text/css">
  .rec-list{
    padding: 0.6rem 1rem;
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
    font-size: 0;
  }
  .rec-list h2{
    font-weight: normal;
    line-height: 2rem;
    font-size: 1rem;
    color: #999;

  }
  .rec-list h2 span{
    color: #333;
    display: inline-block;
    font-weight: bold;
    /*width: 25%;*/
    font-size: 1rem;
  }

</style>

<script>

  import myheader from '../../components/myheader.vue'
  import bottombar from '../../components/bottombar'
    export default{

      components:{
          'my-header':myheader,
          'bottombar':bottombar,
      },
      data(){
          return {
            pwidth:60,
            imgRoot:'',
            isshowalert:false,
            alertmsg:"",
            showload: false,
            loadtext: "正在加载",
            isshowdialog:false,
            eventObj:{event:"",param:""},
            items : [],
            page:1,
            size:20,
            last:false,
            course:"",

         }
      },

      mounted(){
        this.query();
      },
      methods: {
        doclose()
        {
          this.isshowalert=false;
        },
        loadmore()
        {
          this.page= this.page+1;
          this.query();
        },
        query()
        {
          if(this.last)
          {
            return;
          }

          this.showload=true;

          this.axios.post(baseurl+'/api/vedio/playLog.html?currentPage='+this.page+'&pageSize='+this.size).then(
            response =>{
              this.showload=false;
                console.log(response);
              this.last = response.data.last;
              response.data.content.forEach(data=>{
                this.items.push(data);
              });

            },
            response =>{
              this.showload=false
              this.isshowalert=true;;
              this.alertmsg="请求失败，请重试！"
            },
          );

        }


       },

    }
</script>







