  import bottombar from '../components/bottombar'
  export default{
    data(){
      return {
        ismyhome: true,
        isdetailinfo: false,
        stuInfo: '',
      }
    },
    components: {
      'bottombar': bottombar,
    },
    mounted() {

      this.stuInfo = this.getStuInfo();
    },
    methods: {
      outlogin()
      {
        localStorage.clear();
        this.$router.push("/");
      },
    }
