<template>
  <div>

    <div class="top-avatar-wrap">
      <div class="top-avatar">
        <img src=".././assets/logo.png">
      </div>
      <p>驾培视频</p>
    </div>

    <div class="login-group">
      <form>
        <div v-show="btn && !vo.userNo">id不能为空</div>
        <div class="login-input"><i class="iconfont icon-yonghu"></i><input  class="phonenum-input" v-model="vo.userNo" type="text"></div>
        <div class="login-input"><i class="iconfont icon-mima"></i><input class="password-input" v-model="vo.pwd" type="password"></div>
        <div class="login-btn" @click="login()"><a>登录</a></div>
      </form>
    </div>
    <loadpanel :showload="showload" :loadtext="loadtext"></loadpanel>
    <myalert :isshowalert="isshowalert" :alertmsg="alertmsg" :doclose="doclose"></myalert>
  </div>
</template>
<script>

export default {
  name: 'hello',
  data () {
    return {
      btn: false, //true 已经提交过， false没有提交过
      showload: false,
      loadtext: "正在加载",
      alertmsg:"",
      isshowalert:false,

       vo:{
           userNo:'',
           pwd:'',
       }
    }
  },
  components:{


  },
  methods: {
    doclose()
    {
      this.isshowalert=false;
    },
      login()
      {
          if(this.vo.userNo.trim()==''||this.vo.pwd.trim()=='')
          {
            this.alertmsg="请输入用户名和密码";
            this.isshowalert=true;
              return;
          }
        this.showload=true;
        this.axios.post(baseurl+'/api/login.html',this.vo).then(
          response =>{
          this.showload=false;

              if(!response.data.success){
                this.alertmsg=response.data.errMsg;
                this.isshowalert=true;
                return ;
              }
            //记录当前阶段
          //  console.log(response.data.data.stuInfo.toJSON());

            localStorage.setItem("stuInfo",JSON.stringify(response.data.data.stuInfo));
            //console.log(JSON.parse(localStorage.getItem("stuInfo")));
            localStorage.setItem(tokenId,response.data.data.token)
            this.$router.push("/home");
          },
          response =>{
             this.showload=false;
             this.alertmsg="登录失败";
             this.isshowalert=true;
          },
        );

      }
  }
}
</script>
<style scoped>
  .top-avatar-wrap{
    height: 10rem;
    width: 100%;
    background: #000;
    padding-top: 2.4rem;
  }
  .top-avatar-wrap .top-avatar{
    width: 4rem;
    height: 4rem;
    border-radius: 4rem;
    overflow: hidden;
    background: #fff;
    margin: 0 auto;
    text-align: center;
    padding: 1rem;
  }
  .top-avatar-wrap .top-avatar img{
    width: 100%;
    height: 100%;
  }
  .top-avatar-wrap p{
    color: #fff;
    font-size: 1.2rem;
    text-align: center;
    margin-top: 1rem;
  }


.login-group{
  margin-top: 0.5rem;
}
  .login-input{
    height: 3rem;
    width: 100%;
    border-bottom: 1px solid #e5e5e5;
  }
  .login-input i{
    font-size: 2rem;
    color: #c7c7c7;
    padding-left: 1rem;
  }
  .login-input input{
    border: none;
    background: none;
    height: 2rem;
    width: 80%;
    padding: 0 0.5rem;
    font-size: 1.2rem;
    color: #999;
    margin-top: -0.5rem;

  }
  .login-btn{
    padding: 1rem;
  }
  .login-btn a{
    display: block;
    text-align: center;
    width: 100%;
    height: 2.5rem;
    line-height: 2.5rem;
    background: #FF5722;
    border-radius: 0.3rem;
    color: #fff;
    font-size: 1rem;
    font-weight: normal;
  }
  .login-bottom{
    padding: 0 1rem;
  }
  .login-bottom a{
    display: block;
    float: left;
    color: #999;
    font-size: 1rem;
    width: 33.33%;
    height: 1.5rem;
    line-height: 1.5rem;
  }
  .login-bottom a i{
    font-size: 1.4rem;
    color: #999;
    margin-right: 0.4rem;
  }
  .login-bottom a:last-child{
    float: right;
    margin-right: 0;
    text-align: right;
  }
</style>



