<div>
  <div class="myalert-bg" v-show="isshowalert">
    <div class="myalert">
      <p>{{alertmsg}}</p>
      <div class="myalert-btn"><span><a @click="close()">确定</a></span></div>
    </div>
  </div>
</div>
