  .myalert p{
    text-align: center;
    color: #333;
    font-size: 1.2rem;
  }
  .myalert-btn{
    font-size: 0;
    margin-top: 1.6rem;
  }
  .myalert-btn span{
    display: inline-block;
    width: 100%;
  }
  .myalert-btn a{
    color: #fff;
    font-size: 1rem;
    padding: 0.3rem 0;
    display: inline-block;
    width: 100%;
    text-align: center;
    border-radius: 0.4rem;
    background: #FF5722;
  }
