<template>
<div style="padding-top: 3rem; padding-bottom: 4rem;">
<my-header head-title="拍照记录"></my-header>



  <ul>
    <li v-for="item in this.imgData">
      上传时间：{{item.UploadDate}}
      科目：{{item.CourseType}}
      <img :src='imgRoot+"/jpce_upfile/"+item.ImgUrl'  width="100%" height="100px">
    </li>
  </ul>
  <div v-if="last==false" @click="loadmore()" style="text-align: center;font-size: 1rem;color: #999;background: #fff;padding: 0.6rem;">点击加载更多</div>
  <myalert :isshowalert="isshowalert" :alertmsg="alertmsg" :doclose="doclose"></myalert>
  <loadpanel :showload="showload" :loadtext="loadtext"></loadpanel>
  <bottombar></bottombar>
</div>
</template>

<script>

  import myheader from '../../components/myheader.vue'
  import bottombar from '../../components/bottombar'
    export default{

      components:{
          'my-header':myheader,
          'bottombar':bottombar,
      },
      data(){
          return {
            pwidth:60,
            imgRoot:'',
            isshowalert:false,
            alertmsg:"",
            showload: false,
            loadtext: "正在加载",
            isshowdialog:false,
            eventObj:{event:"",param:""},
            items : [],
            page:1,
            size:5,
            last:false,
            courseType:'',
            course:"",
            imgData:[],
         }
      },

      mounted(){
        this.imgRoot=imgRootUrl;
        this.query();
      },
      methods: {
        doclose()
        {
          this.isshowalert=false;
        },
        loadmore()
        {
          this.page= this.page+1;
          this.query();
        },
        query()
        {
          if(this.last)
          {
            return;
          }
          let courseType = this.$route.query.courseType;
          let type = this.$route.query.type;
          this.courseType=courseType;

          this.showload=true;

          this.axios.post(baseurl+'/api/vedio/getImage.html?currentPage='+this.page+'&pageSize='+this.size+'&courseType='+this.courseType + '&type=1').then(
            response =>{
              this.showload=false;
                console.log(response);
              this.last = response.data.last;
              response.data.content.forEach(data=>{
                this.imgData.push(data);
              });

            },
            response =>{
              this.showload=false
              this.isshowalert=true;;
              this.alertmsg="请求失败，请重试！"
            },
          );

        }


       },

    }
</script>







