<template>
  <div style="padding-top:23rem;">
    <!-- <my-header head-title="驾培视频"></my-header> -->
    <div class="vedio-header-wrap-group">
  <div class="vedio-header-wrap">
    <i class="iconfont icon-fanhui" @click="goback"></i><h2></h2>
  </div>
</div>

   <div class="myvideo">
    <div id="player" name="player" style="width:100%;height:300px;"></div>
  </div>
   <div class="subtitle">
   <div class="subtitle-text">
     <h2><span>{{courseVo.courseUnit}}</span>《{{courseVo.courseName}}》</h2>
     <p>课件长度:{{courseVo.courseLength}}秒 播放进度:{{parseInt(playPace)}}秒  有效进度：{{parseInt(studyPace)}}秒</p>
   </div>
   <div class="fanhui" @click="goback"><span>返回</span></div>
   </div>
    <div class="videonumber">
      <p v-for="(key,value) of playList">
        <!--<i v-show="key.StudyType==0">未</i>-->
        <i v-show="key.StudyType==1" style="background:#C5C5C5;">完</i>
        <span :class="{'color-change': isred==value}" @click="chose(key,value)">{{value+1}} </span>
      </p>
    </div>
    <loadpanel :showload="showload" :loadtext="loadtext"></loadpanel>
  </div>
</template>

<script >
  // import myheader from '../../components/myheader.vue';
  //import bcloud from '../../plugins/bcloud.js';
  import api from '../../plugins/letvapi.js';




  export default{
    components:{
      // 'my-header':myheader,

    },
    data(){
       return {
         isred:"",
         showload: false,
         loadtext: "正在加载...",

         studyCourseVo:'',
         courseVo:'',
         studyDayVo:'',
         studyRecordVo:'',
         timeSlice:15000,//计时时间片段

         playPace:0,//播放进度
         studyPace:0,//有效进度

         stopExStatus:0,//stop事件处理状态,0未处理,1已经处理2正在处理
         timerId1:'',
         timerId2:'',
         timerId3:'',
         playList:[],
         postVo:{
           studyDayId:0,
           studyCourseUuid:0,
           pace:0,
           studyRecordId:0,
           playerCode:0,
           errCode:'0',
           errText:''
         },
         letv:new CloudVodPlayer(),
         playerConfig:
           {
             callbackJs:"callback",
             uu:'',
             vu:'',
             start:0,
             auto_play:1,//初始化播放必须配置mustAutoplay
             mustAutoplay:true,
             seekRange:"0,-1",//是否拖动播放
           }
        }
    },
    created()
    {

    },
    mounted()
    {
      this.$nextTick(function () {

        let courseNo = this.$route.query.courseNo;
        let num = courseNo.substring(courseNo.length-2);
        this.isred = parseInt(num)-1;
      })
      //
      this.initParam();



    },beforeDestroy(){
      clearInterval(this.timerId1);
      clearInterval(this.timerId2);
    },
    methods: {
      chose(key,value){
        this.isred=value;
        this.$router.push({path:"/vedio/play",query:{courseNo:key.CourseNo}});
        this.$router.go(0);
      },
      goback () {
        courseType
          :
          this.$router.push({path:"/vedio/list",query:{courseType:this.courseVo.courseType}});

      },
         initParam()
         {
           let courseNo=this.$route.query.courseNo;
           this.axios.post(baseurl+'/api/vedio/play/'+courseNo+'.html').then(
             response =>{


               this.studyCourseVo=response.data.study;
               this.studyDayVo=response.data.day;
               this.studyRecordVo=response.data.record;
               this.courseVo=response.data.course;

               // console.log(response);

               this.playerConfig.uu=this.courseVo.courseLetvUu;
               this.playerConfig.vu=this.courseVo.courseLetvVu;
               this.playerConfig.start=this.studyCourseVo.studyPace;
               this.studyPace=this.studyCourseVo.studyPace;

               this.postVo.studyCourseUuid=this.studyCourseVo.uuid;
               this.postVo.studyDayId=this.studyDayVo.id;
               this.postVo.studyRecordId=this.studyRecordVo.id;
               this.postVo.playerCode=this.studyCourseVo.playerCode;


               this.letv.init(this.playerConfig,'player');
               setTimeout(()=>{
                 this.letv.sdk.seekTo(this.studyCourseVo.studyPace);
               },1000);

               this.timerId1 = setInterval(() => {
                 this.playPace=this.letv.sdk.getVideoTime();
                 if(this.studyCourseVo.studyType==0&&window.playerStatus==2){
                   console.log(window.playerStatus);
                     this.stop();
                 }
               }, 1000);
               setTimeout(()=>{
                if(this.studyCourseVo.studyType==1)
                {
                    console.log("课程已经完成,不计时");
                    return ;
                }
                this.timerId2= setInterval(() => {
                   this.postData();
                 }, this.timeSlice);
               },1000);
               //----------

               this.axios.post(baseurl+'/api/vedio/list.html?courseType='+this.courseVo.courseType).then(
                 response =>{
                   this.playList=response.data;
                   console.log(response);
                 },
                 response =>{
                 },
               );
               //------------
             },
             response =>{
               console.log(response);
               //TODO 弹出对话框,回到列表页
             },
           );
         },
         stop()
         {

           if(this.stopExStatus=='0')
           {
               this.stopExStatus=2;
               //TODO 结束课程请求，异常stopExStatus设置成0未处理
                console.log("状态："+ this.stopExStatus);
             this.showload=true;
             this.axios.post(baseurl+'/api/vedio/over.html',this.postVo).then(
               response =>{
                 this.stopExStatus=1;
                 this.showload=false;
               },response =>{
                 this.stopExStatus=0;
                 this.showload=false;
               });
               // console.log("视频播放结束，处理完成-处理中");npnpm run dev

           }
           //TODO 结束处理
         },
         postData()
         {
             if(window.playerStatus!=1)
             {
               console.log("视频暂停不发送数据");
               return;
             }
             if(this.studyCourseVo.studyType==1)
             {
                 console.log("视频已经完成不用计时");
                 return;
             }
             // 播放進度小於當前進度不發送
           if(this.playPace<=this.studyPace)
           {
               console.log("重复观看不发送数据");
               return;
           }
           //let currentPace= this.studyPace+(this.timeSlice/1000);
           let currentPace= this.playPace;
           if(currentPace>this.courseVo.courseLength)
           {
             currentPace=this.courseVo.courseLength;
           }
           this.postVo.pace=currentPace;
           //console.log(this.postVo);
           this.axios.post(baseurl+'/api/vedio/pace.html',this.postVo).then(
             response => {

                 this.postVo=response.data;
                 if(this.postVo.errCode=='0')
                 {
                   this.studyPace=currentPace;
                 }else
                 {
                   this.letv.sdk.pauseVideo();
                 }
             },
             response =>{
                console.log(response);
                 //TODO 异常，停止播放，强制跳转
                  this.letv.sdk.pauseVideo();
               },
           );
         },



    },

  }
</script>

<style>
.vedio-header-wrap-group{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    z-index: 99;
  }
.vedio-header-wrap{
  position: relative;
  height: 3rem;
  line-height: 3rem;
  background: rgba(0,0,0,0);
  text-align: center;
}
.vedio-header-wrap i{
  position: absolute;
  left: 1rem;
  top: 0rem;
  font-size: 2rem;
  color: #fff;
}
.vedio-header-wrap h2{
  text-align: center;
  font-size: 1.2rem;
  color: #fff;
  font-weight: normal;
}


.subtitle{
  padding: 0.6rem 1rem;
  border-bottom: 1px solid #e5e5e5;
  font-size: 0;
  position: relative;
}
.subtitle-text{
  width: 80%;
  display: inline-block;
}
.fanhui{
  width: 20%;
  display: inline-block;
  text-align: right;
 position: absolute;
 right: 1rem;
 top: 1.5rem;

}
.fanhui span{
  padding: 0.6rem 1rem;
  color: #fff;
  font-size: 1rem;
  background: #FF5722;
  border-radius: 0.3rem;
  vertical-align: middle;
}
.subtitle h2{
  font-style: normal;
  font-weight: bold;
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 0.4rem;
}
.subtitle h2 span{
  color: #FF5722;
}
.subtitle p{
  font-size: 1rem;
  color: #999;
  margin-top: 0.2rem;
}
.myvideo{
  padding: 0rem;
  position: fixed;
  top: 0rem;
  left: 0;
  right: 0;
  z-index: 999;
}
.videonumber{
  padding: 1rem;
}
.videonumber p{
  display: inline-block;
  text-align: center;
  width: 20%;
  margin-bottom: 1rem;
  position: relative;
}
.videonumber p span{
  display: inline-block;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 3.5rem;
  border: 1px solid #e5e5e5;
  line-height: 3.5rem;
  color: #333;
  font-size: 0.8rem;
  text-align: center;
}
.videonumber p i{
  display: inline-block;
  font-style: normal;
  font-size: 0.8rem;
  color: #fff;
  background:#FF5722;
  border-radius: 2rem;
  padding: 0rem 0.3rem;
  position: absolute;
  top: -0.5rem;
  left: 0.4rem;
  border: 3px solid #fff;
}
.color-change{
  border:1px solid #FF5722!important;
  color: #FF5722!important;
  font-weight: bold!important;
}
</style>






