window.playerStatus=0; //0停止 1播放
window.callback=(type,data)=>{
  //console.log(type);
  switch(type)
  {
    case "videoStart":
      window.playerStatus=1;
      break;
    case "videoStop":
      window.playerStatus=2;
      break;
    case "videoPause":
      window.playerStatus=0;
      break;
    case "videoResume":
      window.playerStatus=1;
      break;
  }
}




