<script>
  import myheader from '../components/myheader.vue'
  import bottombar from '../components/bottombar'
  export default{
    components: {
      'my-header': myheader,
      'bottombar': bottombar,
    },
    data()
    {
      return{
        showload: false,
        loadtext: "正在加载",
        vo:''
      }
    },
    mounted(){
      this.showload=true;
      this.axios.post(baseurl+'/api/vedio/getCourseType.html').then(
        response =>{
          this.showload=false;
          this.vo=response.data;
          console.log(this.vo);
        },
        response =>{
          this.showload=false;
        },
      );
    },
    methods: {

    }
  }

</script>
