<template>
<div class="header-wrap-group">
  <div class="header-wrap">
    <i class="iconfont icon-fanhui" @click="goback"></i><h2>{{headTitle}}</h2>
  </div>
</div>
</template>

<style>
  .header-wrap-group{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    z-index: 99;
  }
.header-wrap{
  position: relative;
  height: 3rem;
  line-height: 3rem;
  background: #FF5722;
  text-align: center;
}
.header-wrap i{
  position: absolute;
  left: 1rem;
  top: 0rem;
  font-size: 2rem;
  color: #fff;
}
.header-wrap h2{
  text-align: center;
  font-size: 1.2rem;
  color: #fff;
  font-weight: normal;
}
</style>

<script>
export default {

  props: ['headTitle'],
  mounted(){

  },
  methods: {
    goback () {
        this.$router.go(-1)
    }
  }
}

</script>




