<template>
<div>
  <div class="panel-bg" v-show="isshowdialog">
    <div class="panel1">
      <p>{{dialogmsg}}</p>
      <div class="panel1-btn"><span><a @click="cancel()">取消</a></span><span><a @click="confirm()">确定</a></span></div>
    </div>
  </div>
</div>
</template>
<script>
export default{
    props:['docancel','doconfirm','dialogmsg','isshowdialog','eventObj'],
    data(){
        return{}
    },
  mounted(){

  },
  methods:{
        cancel(){
            this.docancel();
        },
    confirm(){
            let eventObj = this.eventObj;
            this.doconfirm(eventObj);
    }
  }
}

</script>
<style>
  .panel-bg{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    height: 100%;
    background: rgba(0,0,0,0.6);
    z-index: 999;
    padding: 0 1rem;

  }
  .panel1{
    background: #fff;
    border-radius: 0.4rem;
    padding: 2rem;
    margin-top: 12rem;
  }
  .panel1 p{
    text-align: center;
    color: #333;
    font-size: 1.2rem;
  }
  .panel1-btn{
    font-size: 0;
    margin-top: 1.6rem;
  }
  .panel1-btn span{
    display: inline-block;
    width: 50%;
  }
  .panel1-btn span:first-child{
    text-align: left;
  }
  .panel1-btn span:last-child{
    text-align: right;
  }
  .panel1-btn a{
    color: #fff;
    font-size: 1rem;
    padding: 0.3rem 0;
    display: inline-block;
    width: 90%;
    text-align: center;
    border-radius: 0.4rem;
  }
  .panel1-btn span:first-child a{
    background: #e5e5e5;
    color: #333;
  }
  .panel1-btn span:last-child a{
    background: #FF5722;
  }
</style>






